.container {
  position: relative;
  height: 64px;
  overflow: hidden;
  line-height: 64px;
  background: #002140;
  transition: all 0.3s;

  display: flex;
  align-items: center;

  white-space: nowrap;
}

.logo {
  margin: auto;

  img {
    width: 36px;
    height: 36px;
    border-radius: 18px;
  }
}
