blockquote {
  padding: 10px 20px;
  border-left: 2px solid #009A61;
  background: #F6F6F6;
  color: #555;
  font-size: 1em; }

code {
  padding: 2px 4px;
  font-size: 90%;
  color: #c7254e;
  background-color: #f9f2f4;
  border-radius: 4px; }

[class*=language-] {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  color: #abb2bf;
  background: #282c34;
  position: relative;
  border-radius: 6px; }

[class*=language-]:before {
  content: "";
  position: absolute;
  z-index: 2;
  top: 5px;
  right: 3px;
  /* width: 3.5rem; */
  /* height: 100%; */
  border-radius: 6px 0 0 6px;
  border-right: 1px solid rgba(0, 0, 0, 0.66);
  background-color: #282c34; }

[class~=language-js]:before {
  content: "js"; }

[class~=language-ts]:before {
  content: "ts"; }

