.header {
  position: relative;
  padding: 0;
  background: #fff;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
}

.trigger {
  height: 64px;
  padding: 22px 24px;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.3s, padding 0s;

  &:hover {
    background: rgba(0, 0, 0, 0.025);
  }
}

.account {
  float: right;
  margin: 0 8px;
  cursor: pointer;
  &:hover {
    background: rgba(0, 0, 0, 0.025);
  }
}
.avatar {
  margin: 0 8px;
}
.name {
  margin: 0 8px;
}
